import {lazy, FC, Suspense} from 'react'
import {Route, Routes, Navigate} from 'react-router-dom'
import {MasterLayout} from '../../_metronic/layout/MasterLayout'
import TopBarProgress from 'react-topbar-progress-indicator'
import {DashboardWrapper} from '../pages/dashboard/DashboardWrapper'
import {getCSSVariableValue} from '../../_metronic/assets/ts/_utils'

const PrivateRoutes = () => {/* 
  const BuilderPageWrapper = lazy(() => import('../pages/layout-builder/BuilderPageWrapper')) */
  const ProfilePage = lazy(() => import('../modules/profile/ProfilePage'))
  const AdminManage = lazy(() => import('../modules/widgets/AdminManage'))
  const NotiPage = lazy(() => import('../modules/notifications/NotiPage'))
  const SettingPage = lazy(()=> import('../modules/settings/SettingPage'))
  return (
    <Routes>
      <Route element={<MasterLayout />}>
        {/* Redirect to Dashboard after success login/registartion */}
        <Route path='auth/*' element={<Navigate to='/profile/partner' />} />
        {/* Pages */}
        <Route path='dashboard' element={<DashboardWrapper />} />
        {/* Lazy Modules */}
        <Route
          path='profile/*'
          element={
            <SuspensedView>
              <ProfilePage />
            </SuspensedView>
          }
        />
       
        <Route
          path='admin-manager/*'
          element={
            <SuspensedView>
              <AdminManage />
            </SuspensedView>
          }
        />
      
        <Route
          path='notifications/*'
          element={
            <SuspensedView>
              <NotiPage />
            </SuspensedView>
          }
        />
        
        <Route
          path='admin-settings/*'
          element={
            <SuspensedView>
              <SettingPage />
            </SuspensedView>
          }
        />
        {/* Page Not Found */}
        <Route path='*' element={<Navigate to='/profile/partner' />} />
      </Route>
    </Routes>
  )
}

const SuspensedView: FC = ({children}) => {
  const baseColor = getCSSVariableValue('--bs-primary')
  TopBarProgress.config({
    barColors: {
      '0': baseColor,
    },
    barThickness: 1,
    shadowBlur: 5,
  })
  return <Suspense fallback={<TopBarProgress />}>{children}</Suspense>
}

export {PrivateRoutes}
